body {
  background-color: #f0f8ff;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.weather-dashboard {
  text-align: center;
  background-color: #fff;
  border-radius: 10px;
  width: 300px;
  padding: 20px;
  box-shadow: 0 4px 6px #0000001a;
}

.input-section {
  margin-bottom: 20px;
}

input {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 70%;
  margin-right: 10px;
  padding: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
}

button:hover {
  background-color: #0056b3;
}

.weather-result {
  margin-top: 20px;
}

.weather-result p {
  margin: 10px 0;
}
